import React from "react";
import { Redirect, withRouter } from "react-router-dom";
import ApiManager from "../../../service/store-service";
import { setNewAccess } from "../../../service/jwt";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { USER_LOADED_FAIL, USER_LOADED_SUCCESS } from "../../actions/actions";

class Profile extends React.Component {
  server = new ApiManager();
  componentDidMount() {
    const { USER_LOADED_FAIL, USER_LOADED_SUCCESS } = this.props;
    this.server
      .checkToken(
        localStorage.getItem("jwtToken"),
        localStorage.getItem("refreshToken")
      )
      .then((access) => {
        setNewAccess(access);
        USER_LOADED_SUCCESS();
      })
      .catch(() => USER_LOADED_FAIL());
  }
  render() {
    const { isAuth } = this.props;
    if (isAuth) {
      return <span>Hello from profile page number {this.props.userId}</span>;
    }
    return <Redirect to="/login" />;
  }
}

const putActionToProps = (dispatch) => {
  return bindActionCreators(
    { USER_LOADED_FAIL, USER_LOADED_SUCCESS },
    dispatch
  );
};

const putStateToProps = (state) => {
  return { isAuth: state.Auth.isAuth, isLogged: state.Auth.isLogged };
};

export default connect(putStateToProps, putActionToProps)(withRouter(Profile));
