const InitialState = {
  access: "",
  refresh: "",
  isAuth: false,
  isLogged: false,
};

const Auth = (state = InitialState, action) => {
  switch (action.type) {
    case "SUCCESS_LOGGED":
      return {
        ...state,
        isLogged: true,
      };
    case "USER_LOADED_SUCCESS":
      return {
        ...state,
        isAuth: true,
      };
    case "USER_LOADED_FAIL":
      return {
        ...state,
        isAuth: false,
      };
    case "SET_JWT":
      return {
        ...state,
        access: action.data.access,
        refresh: action.data.refresh,
      };
    default:
      return state;
  }
};

export default Auth;
