import axios from "axios";

export default class ApiManager {
  base_url = "http://127.0.0.1:8000/api/";

  async getResource(url) {
    const rawData = await fetch(`${this.base_url}${url}`);
    if (rawData.ok) {
      return await rawData.json();
    } else {
      throw new Error("oops");
    }
  }
  verifyJWTToken = async (token) => {
    const config = {
      headers: {
        "Content-Type": "application/json",
      },
    };
    const body = {
      token: token,
    };
    try {
      const result = await axios.post(
        `http://127.0.0.1:8000/auth/jwt/verify/`,
        body,
        config
      );
      return result.status;
    } catch {
      return 401;
    }
  };

  refreshToken = async (refresh) => {
    let data1 = refresh.replace(/"/g, "");
    const config = {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    };
    const body = {
      refresh: data1,
    };
    const result = await axios.post(
      `http://127.0.0.1:8000/auth/jwt/refresh/`,
      body,
      config
    );
    return result;
  };

  checkToken = async (access, refresh) => {
    const accessChecking = await this.verifyJWTToken(access);
    console.log(accessChecking);
    if (accessChecking === 401) {
      const newAccess = await this.refreshToken(refresh);
      return newAccess.data;
    } else {
      return;
    }
  };

  getProfile = async (data, id) => {
    let data1 = data.replace(/"/g, "");
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${data1}`,
        Accept: "application/json",
      },
    };
    console.log("ok");
    try {
      const rawData = await axios.get(`${this.base_url}profile/${id}/`, config);
      console.log(rawData.data);
    } catch {
      console.log("bad request");
    }
  };

  getListOfItem = async (url_tag) => {
    const res = await this.getResource(url_tag);
    return await res;
  };

  getProductByID = async (id) => {
    const res = await this.getResource(`products/${id}`);
    return await res;
  };
}
