import React from "react";
import ApiManager from "../service/store-service";
import { bindActionCreators } from "redux";
import { setJWTToken } from "../components/actions/actions";
import { connect } from "react-redux";

import NavBar from "./header/navbar";
import ProductPage from "./pages/products/product-page";
import DetailProductPage from "./pages/detail-product/detail";
import Login from "./auth/login";
import Profile from "./pages/profile/profile";

import { Route } from "react-router-dom";

class App extends React.Component {
  api = new ApiManager();

  componentDidMount() {
    const access = localStorage.getItem("jwtToken");
    const refresh = localStorage.getItem("refreshToken");
  }

  render() {
    return (
      <div>
        <NavBar />
        <Route path="/" component={ProductPage} exact />
        <Route
          path="/products/:id"
          render={({ match }) => {
            const { id } = match.params;
            return <DetailProductPage itemid={id} />;
          }}
          exact
        />
        <Route path="/login" component={Login} exact />
        <Route
          path="/profile/:id"
          render={({ match }) => {
            const { id } = match.params;
            return <Profile userId={id} />;
          }}
          exact
        />
      </div>
    );
  }
}

const putActionToProps = (dispatch) => {
  return bindActionCreators({ setJWTToken }, dispatch);
};

const putStateToProps = ({ products, loading, jwt }) => {
  return { jwt };
};

export default connect(putStateToProps, putActionToProps)(App);
